import React from 'react';
import {getToken} from '../tils/Auth';
import {Button, FormControl, FormGroup, Modal, Table} from 'react-bootstrap';
import axios from 'axios';
import "../../styles/css/Form.css";

export default class CrudPerguntas extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      perguntas: [], newPergunta: '',
      questionario: [], questionarios: [], questionarioId: '',
      modified: '', show: false, idChange: ''
    };

    var config = {
      headers: {
        'Authorization': 'Bearer ' + getToken()
      }
    }

    this.onDelete = (evento) => {
      evento.persist();
          axios.delete('https://bemapi.herokuapp.com/pergunta/'+ evento.target.name, config)
          .then(res =>{
            if (res.data.status === 'success'){
              var array = [...this.state.perguntas];
              var index = evento.target.getAttribute('keyId');
              array.splice(index,1);
              this.setState({
                perguntas: array
              })
            }
          })
          .catch(error => {
            console.log(error.response);
          })
    }

    this.onCreate = (evento) => {
          const pergunta = {
              descricao: this.state.newPergunta,
              questionario_id: this.state.questionarioId
          }
          axios.post('https://bemapi.herokuapp.com/pergunta', pergunta, config)
          .then(res => {
              const dataTest = this.state.perguntas;
              const object = {
                  descricao: this.state.newPergunta,
                  id: res.data.id
              }
              dataTest.push(object);
              this.setState({perguntas : dataTest})
          })
          .catch(error => {
            console.log(error.response);
          })
    }

    this.onChange = (evento) => {
      const pergunta = {
          descricao: this.state.modified
      }
      axios.put('https://bemapi.herokuapp.com/pergunta/' + this.state.idChange, pergunta, config)
      .then(res =>{

      })
      .catch(error =>{
        console.log(error.response);
      })
    }

    this.onShowModal = (evento) => {
      this.setState({
          show: true,
          idChange: evento.target.name
      });
    }

    this.onCloseModal = () =>{
      this.setState({
				show: false
			});
    }

    this.handleChangeQues = (evento) => {
      evento.persist();

      this.setState({
        [evento.target.id]: evento.target.value
      });

      axios.get('https://bemapi.herokuapp.com/questionario/' + evento.target.value + '/perguntas')
  		.then((response) => {
  			this.setState({
  				perguntas: response.data
  			});
  		})
  		.catch((error) =>{
  			console.log(error.response);
  		});
    }
  }



  componentWillMount(){
    axios.get('https://bemapi.herokuapp.com/questionario')
		.then((response) => {
			this.setState({
				questionarios: response.data
			});
		})
		.catch((error) =>{
			console.log(error.response);
		});

	}

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  render(){
		return(
			<div className="container page">
        <div className="add">
        <FormGroup controlId="questionarioId">
          <h3>Questionário</h3>
          <FormControl onChange={this.handleChangeQues} componentClass="select" placeholder="select">
                  <option value=''>Escolha o Questionário</option>
              {
                this.state.questionarios.map((row,index) => (
                  <option value={row.id} keyId={index}>{row.titulo}</option>
                ))
              }
          </FormControl>
        </FormGroup>
        <h1>Adicionar Nova Pergunta</h1>
          <FormGroup controlId="newPergunta" bsSize="large">
              <FormControl className="form-control"
                autoFocus
                type="text"
                value={this.state.newPergunta}
                onChange={this.handleChange}
              />
              <Button bsStyle="success" bsSize="large" onClick={this.onCreate} block>
                Adicionar
              </Button>
          </FormGroup>
        </div>
        <div className="board">
            <h1>Remover Perguntas</h1>
			<Table striped bordered condensed hover>
				<thead>
					<tr>
					  <th>ID</th>
					  <th>DESCRIÇÃO</th>
					</tr>
				</thead>
    			{
    				this.state.perguntas.map((row,index) => (
					<tbody>
						<tr>
							<td>{row.id}</td>
							<td>{row.descricao}</td>
							<td><Button bsStyle="warning" className="btn-right" name={row.id} keyId={index} onClick={this.onShowModal}>Editar</Button></td>
							<td><Button bsStyle="danger" className="btn-right" name={row.id} keyId={index} onClick={this.onDelete}>Excluir</Button></td>
						</tr>
					</tbody>
    			))
    			}
			</Table>
          <Modal show={this.state.show}>
            <Modal.Body>
            <h3>Descrição</h3>
            <FormGroup controlId="modified" bsSize="large">
              <FormControl className="form-control"
                autoFocus
                type="text"
                value={this.state.modified}
                onChange={this.handleChange}
              />
            </FormGroup>
            </Modal.Body>
            <Modal.Footer>
            <Button bsStyle="success" onClick={this.onChange}>Confirmar</Button>
            <Button onClick={this.onCloseModal}>Cancelar</Button>
           </Modal.Footer>
          </Modal>
        </div>
			</div>
		)
	}
}



