import React, { Component } from 'react';
import {
	BrowserRouter as Router,
	Route, Redirect, Switch
} from 'react-router-dom';

import {isAdmin} from './components/tils/Auth'
import CrudPerguntas from './components/pages/CrudPerguntas'
import CrudQuestionario from './components/pages/CrudQuestionario'
import Header from './components/headerComponent/Header';
import Footer from './components/footerComponent/Footer';
import Form from './components/pages/Form';
import Login from './components/pages/Login';
import Ansiedade from './components/pages/Ansiedade';
import Depressao from './components/pages/Depressao';
import Estresse from './components/pages/Estresse';
import Psicologos from './components/pages/Psicologos';
import CadastroPsi from './components/pages/CadastroPsi';
import CadastroAdm from './components/pages/CadastroAdm';
import HomePage from './components/pages/HomePage';

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      isAdmin() ? (
        <Component {...props} />
      ) : (
        <Redirect
          to='/'
        />
      )
    }
  />
);

class App extends Component {
  render() {
    return (
    <Router>
      <div className="App">
        <Header />
				<Switch>
          <Route exact path="/" component={HomePage} />
          <Route exact path="/Form" component={Form} />
          <Route exact path="/Login" component={Login} />
					<Route exact path="/Cadastro" component={CadastroPsi} />
					<Route exact path="/Psicologos" component={Psicologos} />
					<Route exact path="/Ansiedade" component={Ansiedade}/>
          <Route exact path="/Depressao" component={Depressao}/>
          <Route exact path="/Estresse" component={Estresse}/>
					<PrivateRoute path="/CrudQuestionario" component={CrudQuestionario} />
					<PrivateRoute path="/CrudPerguntas" component={CrudPerguntas} />
          <PrivateRoute path="/CadastroAdm" component={CadastroAdm}/>
					<Route component={HomePage} />
				</Switch>
        <Footer />
      </div>
    </Router>
    );
  }
}

export default App;



