import React from 'react';
import {getToken} from '../tils/Auth';
import {Button, FormControl, FormGroup, Modal, ControlLabel, Table} from 'react-bootstrap';
import axios from 'axios';
import "../../styles/css/Form.css";

export default class CrudPerguntas extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      questionarios: [], quesTitulo: '', quesDescricao: '',
      modifiedTitulo: '', modifiedDesc: '', show: false, idChange: ''
    };

    var config = {
      headers: {
        'Authorization': 'Bearer ' + getToken()
      }
    }

    this.onDelete = (evento) => {
        evento.persist();
        axios.delete('https://bemapi.herokuapp.com/questionario/'+ evento.target.name, config)
        .then(res =>{
          if (res.data.status === 'success'){
            var array = [...this.state.questionarios];
            var index = evento.target.getAttribute('keyId');
            array.splice(index,1);
            this.setState({
              questionarios: array
            })
          }
        })
        .catch(error => {
          console.log(error.response);
        })
      }


    this.onCreate = (evento) => {
        evento.persist();
        const questionario = {
            titulo: this.state.quesTitulo,
            descricao: this.state.quesDescricao
        }
        axios.post('https://bemapi.herokuapp.com/questionario', questionario, config)
        .then(res => {
            console.log(questionario);
            const dataTest = this.state.questionarios;
            const object = {
                titulo: this.state.quesTitulo,
                descricao: this.state.quesDescricao,
                id: res.data.id
            }
            dataTest.push(object);
            this.setState({questionarios : dataTest})
        })
        .catch(error => {
          console.log(error);
          console.log(error.response);
        })
      }

    this.onChange = (evento) => {
      const questionario = {
          descricao: this.state.modifiedDesc
      }
      axios.put('https://bemapi.herokuapp.com/questionario/' + this.state.idChange, questionario, config)
      .then(res =>{

      })
      .catch(error =>{
        console.log(error.response);
      })
    }

    this.onShowModal = (evento) => {
      this.setState({
          show: true,
          idChange: evento.target.name
      });
    }

    this.onCloseModal = () =>{
      this.setState({
				show: false
			});
    }
  }



  componentWillMount(){
    axios.get('https://bemapi.herokuapp.com/questionario')
		.then((response) => {
			this.setState({
				questionarios: response.data
			});
		})
		.catch((error) =>{
			console.log(error.response);
		});
	}

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
    console.log(this.state);
  }

  render(){
		return(
			<div className="container page">
        <div className="add">
        <h1>Adicionar Questionário</h1>
            <FormGroup controlId="quesTitulo"  bsSize="large">
              <ControlLabel>Título</ControlLabel>
              <FormControl className="form-control"
                autoFocus
                type="text"
                value={this.state.quesTitulo}
                onChange={this.handleChange}
              />
            </FormGroup>
            <FormGroup controlId="quesDescricao" bsSize="large">
              <ControlLabel>Descrição</ControlLabel>
              <FormControl componentClass="textarea" controlId="quesDescricao" className="form-control"
                value={this.state.quesDescricao}
                onChange={this.handleChange}
              />
              <Button onClick={this.onCreate} bsStyle="success" bsSize="large" block>
                Adicionar
              </Button>
            </FormGroup>
          </div>
        <div className="board">
            <h1>Remover Questionário</h1>
			<Table striped bordered condensed hover>
				<thead>
					<tr>
					  <th>ID</th>
					  <th>TÍTULO</th>
					  <th>DESCRIÇÃO</th>
					</tr>
				</thead>
          {

            this.state.questionarios.map((row,index) => (
               <tbody>
        				<tr>
        				  <td>{row.id}</td>
        				  <td>{row.titulo}</td>
        				  <td>{row.descricao}</td>
        				  <td><Button bsStyle="warning" className="btn-right" name={row.id} keyId={index} onClick={this.onShowModal}>Editar</Button></td>
        				  <td><Button bsStyle="danger" className="btn-right" name={row.id} keyId={index} onClick={this.onDelete}>Excluir</Button></td>
        				</tr>
      			  </tbody>
			))

          }
		  </Table>
      <Modal show={this.state.show}>
        <Modal.Body>
        <h3>Título</h3>
        <FormGroup controlId="modifiedTitulo" bsSize="large">
          <FormControl className="form-control"
            autoFocus
            type="text"
            value={this.state.modifiedTitulo}
            onChange={this.handleChange}
          />
        </FormGroup>
        <h3>Descrição</h3>
        <FormGroup controlId="modifiedDesc" bsSize="large">
          <FormControl className="form-control"
            classComponent="textarea"
            autoFocus
            type="text"
            value={this.state.modifiedDesc}
            onChange={this.handleChange}
          />
        </FormGroup>
        </Modal.Body>
        <Modal.Footer>
        <Button bsStyle="success" onClick={this.onChange}>Confirmar</Button>
        <Button onClick={this.onCloseModal}>Cancelar</Button>
       </Modal.Footer>
      </Modal>
        </div>
			</div>
		)
	}
}



