import React from 'react';
import {Pager} from "react-bootstrap";
import "../../styles/css/Transtornos.css";
import image2 from '../../styles/images/ansiedade.jpg'

export default class Ansiedade extends React.Component{
    render(){
      return(
          <div class="container">
            <img class="image" src={image2}/>
            <h1 class="font">Ansiedade</h1>
            <div class="row">
              {/*<h1>Ansiedade</h1>*/}
                <div class="col-md-4"><h4 class="text-center sub-title">Causas</h4>
                  <p class="text">Esses dois aspectos, tanto a ansiedade quanto o medo, não surgem na vida da pessoa por uma escolha.
                    Acredita-se que vivências interpessoais e problemas na primeira infância possam ser importantes causas desses sintomas.
                    Além disso, existem causas biológicas como anormalidades químicas no cérebro ou distúrbios hormonais.
                    Ansiedade é um estado emocional que se adquire como consequência de algum ato.<p/>
                    Todos nós temos uma consciência que nos permite distinguir o certo do errado quando realizamos algo que a nossa consciência diz ser errado,
                    como adultério, assassinato, ou desacatar alguém que sempre nos fez o bem, sem um motivo real aparente, isso nos leva a uma consciência pesada,
                    é como se a cabeça ficasse literalmente com um peso extra ou sentimento de culpa que impede o nosso equilíbrio.
                    Embora para a medicina alopata a ansiedade não seja doença, para a medicina chinesa a ansiedade é uma doença da energia do corpo,
                    pois não existe órgão lesado que produza marcadores bioquímicos como: hormônios, enzimas que indique lesão etc.
                    Mas o desequilíbrio do organismo existe. O desequilíbrio ocorrerá futuramente com o agravo do quadro ou com a evolução do agudo - crônico - degenerativo.</p>
                </div>
                <div class="col-md-4"><h4 class="text-center sub-title">Consequências</h4>
                  <p class="text">Todas as pessoas podem sentir ansiedade, principalmente com a vida atribulada atual.
                  A ansiedade acaba tornando-se constante na vida de muitas pessoas. Dependendo do grau ou da frequência, pode se tornar patológica e acarretar muitos problemas posteriores, como o transtorno da ansiedade. Portanto, nem sempre é patológica.
                  Ter ansiedade ou sofrer desse mal faz com que a pessoa perca uma boa parte da sua autoestima, ou seja,
                  ela deixa de fazer certas coisas porque se julga ser incapaz de realizá-las. Dessa forma,
                  o termo ansiedade está de certa forma ligado à palavra medo, sendo assim a pessoa passa a ter medo de errar quando da realização de
                  diferentes tarefas, sem mesmo chegar a tentar.</p>
                  <p class="text">Em alguns casos, a ansiedade é capaz de intensificar o que o indivíduo está sentindo,
                  deixando de apresentar sintomas únicos e passando a aumentar aqueles naturalmente produzidos pelo sistema nervoso.
                  Ou seja: se a pessoa sente medo, ela sentirá muito medo; se a pessoa se sente triste, ela se sentirá muito triste;
                  as vezes sentimentos comuns como gostar de algo ou alguém podem ter um grande impacto sobre portadores de ansiedade,
                  e querer algo pode tornar-se uma necessidade com o passar do tempo.</p>
                  <p class="text">A Ansiedade em níveis muito altos, ou quando apresentada com a timidez ou depressão, impede que a pessoa desenvolva seu potencial intelectual.
                  O aprendizado é bloqueado e isso interfere não só no aprendizado da educação tradicional, mas na inteligência social.
                  O indivíduo fica sem saber como se portar em ocasiões sociais ou no trabalho, o que pode levar a estagnação na carreira.</p>
                </div>
                <div class="col-md-4"><h4 class="text-center sub-title">Sintomas</h4>
                  <ul class="text">
                    <li>Fadiga</li>
                    <li>Insônia</li>
                    <li>Falta de ar ou sensação de sufoco</li>
                    <li>Picadas nas mãos e nos pés</li>
                    <li>Confusão</li>
                    <li>Instabilidade ou sensação de desmaio</li>
                    <li>Dores no peito e palpitações</li>
                    <li>Afrontamentos, arrepios, suores, frio, mãos úmidas</li>
                    <li>Boca seca</li>
                    <li>Contrações ou tremores incontroláveis</li>
                    <li>Tensão muscular, dores</li>
                    <li>Necessidade urgente de defecar ou urinar</li>
                    <li>Dificuldade em engolir</li>
                    <li>Sensação de ter um "nó" na garganta</li>
                    <li>Dificuldades para relaxar</li>
                    <li>Dificuldades para dormir</li>
                    <li>Leve tontura ou vertigem</li>
                    <li>Vômitos incontroláveis</li>
                    <li>Sensação de impotência</li>
                  </ul>
                </div>
              </div>
          </div>
        );

        <Pager>
        <Pager.Item href="#">Previous</Pager.Item>{' '}
        <Pager.Item href="#">Next</Pager.Item>
        </Pager>;
    }
}



