import React from 'react';
import {Table} from 'react-bootstrap';
import axios from 'axios';
import "../../styles/css/Form.css";

export default class Psicologos extends React.Component{
  constructor(props){
    super(props)

    this.state = {
      psicologos: []
    };
  }

  componentWillMount(){
    axios.get('https://bemapi.herokuapp.com/usuario/psicologos')
    .then((response) => {
			this.setState({
				psicologos: response.data
			});
		})
		.catch((error) =>{
			console.log(error.response);
		});
  }

  render(){
    return(
        <div className="container">
        <h1>Psicólogos</h1>
          <Table striped bordered condensed hover>
            <thead>
            <tr>
              <th>Nome</th>
              <th>Telefone</th>
              <th>Endereco</th>
            </tr>
            </thead>
          {
            this.state.psicologos.map((row,index) => (
              <tbody>
               <tr>
                 <td>{row.usuario.nome}</td>
                 <td>{row.telefone}</td>
                 <td>{row.endereco}</td>
               </tr>
             </tbody>
            ))
          }
        </Table>
        </div>
    )
  }
}



