import React from 'react';
import {isAdmin, isLoggedIn, logout} from '../tils/Auth'
import {
	Link
} from 'react-router-dom';


export default class Header extends React.Component {
	render(){
		return(
			<header>
			<nav className="navbar navbar-expand-lg bg-blue">
		      <div className="container">
		        <Link to = "/"><a className="navbar-brand bem_logo link-nav">BEM</a></Link>
		        <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
		          <span className="navbar-toggler-icon"></span>
		        </button>
		        <div className="collapse navbar-collapse" id="navbarResponsive">
		          <ul className="nav navbar-nav ml-auto">
		            <li className="nav-item">
		              <Link to ="/Form"><a className="nav-link link-nav" >Formulário</a></Link>
		            </li>
								<li className="nav-item dropdown">
 							 <a className="nav-link link-nav dropdown-toggle" data-toggle="dropdown" href="">Transtornos
 							 <span></span></a>
 							 <ul className="dropdown-menu">
 								 <li><Link to ="/Ansiedade">Ansiedade</Link></li>
 								 <li><Link to ="/Depressao">Depressão</Link></li>
 								 <li><Link to ="/Estresse">Estresse</Link></li>
 							 </ul>
 							 </li>
								{
								(!isAdmin()) ? (<a></a>) : (<li className="nav-item dropdown">
				        <a className="nav-link link-nav dropdown-toggle" data-toggle="dropdown" href="">Admin
				        <span></span></a>
				        <ul className="dropdown-menu">
				          <li><Link to ="/CrudQuestionario">Questionario</Link></li>
									<li><Link to ="/CrudPerguntas">Perguntas</Link></li>
				          <li><Link to ="/CadastroAdm">Cadastro Admin</Link></li>
				        </ul>
				      	</li>)
								}
								<li className="nav-item">
								{
		              (!isLoggedIn()) ? (<Link to ="/Login"><a className="nav-link link-nav">Login</a></Link>) : (<Link to ="/"><a className="nav-link link-nav" onClick={() => logout()}>Logout</a></Link>)
								}
		            </li>
		          </ul>
		        </div>
		      </div>
		    </nav>
		    </header>
			);
	}
}



