import React from 'react';
import Carousel from 'nuka-carousel';
import image from '../../styles/images/bemtest.png'
import image2 from '../../styles/images/ansiedade.jpg'

export default class HomePage extends React.Component {
	state = {
	    slideIndex: 0
	};

	render(){
		return(
			<div className="HomePage">
			<Carousel
				autoplay={true}
        slideIndex={this.state.slideIndex}
				beforeSlide={slideIndex => this.setState({ slideIndex })}
      >
        <img src={image2} onLoad={() => {window.dispatchEvent(new Event('resize'));}} />
        <img src={image} />
      </Carousel>
			</div>
		);
	}
}



