import React from 'react';
import "../../styles/css/Transtornos.css";
import image from '../../styles/images/depre.jpg'

export default class Depressao extends React.Component{
    render(){
      return(
        <div class="container">
          <img class="image" src={image}/>
          <h1 class="font">Depressão</h1>
          <div class="row">
            <div class="col-md-4"><h4 class="text-center sub-title">O que é?</h4>
              <p class="text">Depressão é um estado depressivo de humor, caracterizado por aversão à atividade,
              que pode afetar os pensamentos, comportamentos, sentimentos e o bem-estar de uma pessoa.
              As pessoas deprimidas podem sentir-se tristes, ansiosas, vazias, desesperadas, preocupadas,
              impotentes, inúteis, culpadas, irritadas, magoadas ou inquietas. Podem perder o interesse em atividades
              que antes eram prazerosas, podem perder o apetite ou comer demais, apresentar problemas de concentração,
              dificuldade para lembrar detalhes ou tomar decisões. Problemas de
              insônia, sono excessivo, fadiga, perda de energia, mudança na alimentação, sofrimento,
              dores ou problemas digestivos resistentes a tratamento também podem estar presentes.</p>
              <p class="text">O humor deprimido não é necessariamente um transtorno psiquiátrico.
              Pode ser uma reação passageira a determinados acontecimentos ou consequência de uma dada condição
              médica ou, ainda, efeito colateral de medicamentos ou tratamentos médicos. O humor deprimido é também
              a característica principal ou associada a certas síndromes psiquiátricas tais como depressão clínica.</p>
              <h4 class="text-center sub-title">Estastiticas</h4>
              <ol class="breadcrumb text">
                Mais de 2 milhões casos por ano (Brasil)
              </ol>
            </div>
            <div class="col-md-4"><h4 class="text-center sub-title">Causas</h4>
              <p><strong class="sub-title">Eventos Pessoais</strong></p>
                <p class="text">Mudanças e eventos de vida que podem precipitar o humor deprimido incluem o parto,
                menopausa,sedentarismo, dificuldades financeiras, problemas de trabalho, a perda
                de um amigo ou ente querido, casamento, desastres naturais, tais como terremotos,
                furacões, tornados etc, problemas de relacionamento,problemas de família ou até mesmo
                falta de atenção familiar, separação, luto ou lesão causada por catástrofe</p>
                <p><strong class="sub-title">Doencas Nao Psiquiatricas</strong></p>
                <p class="text">O humor deprimido pode ser o resultado de uma série de doenças infecciosas,
                condições neurológicas e problemas fisiológicos, incluindo hipoandrogenismo (nos homens
                ), doença de Addison, doença de Lyme, esclerose múltipla, dor crónica, acidente vascular
                cerebral, diabetes, apnéia do sono e ritmo circadiano perturbado. Muitas vezes, é um dos
                primeiros sintomas de hipotireoidismo (diminuição da atividade da glândula tireóide).</p>
                <p><strong class="subtitle">Sindromes Psiquiatricas</strong></p>
              <ul class="text">
                <li>Experiências estressantes (físicas ou emocionais)</li>
                <li>Trauma na primeira infância</li>
                <li>Abuso de álcool ou drogas</li>
                <li>Problemas de saúde ou dores crônicas</li>
                <li>Deficiência de neurotransmissores</li>
                <li>Alterações na estrutura do cérebro</li>
                <li>Doenças cerebrais</li>
              </ul>
            </div>
            <div class="col-md-4"><h4 class="text-center sub-title">Sintomas</h4>
            <p class="text">Requer um diagnóstico médico;</p>
            <p class="text">A sensação persistente de tristeza ou perda de interesse que caracteriza
            a depressão pode levar a uma variedade de sintomas físicos e
            comportamentais. Estes podem incluir alterações no sono, apetite, nível
            de energia, concentração, comportamento diário ou autoestima.</p>
            <strong class="sub-title">As pessoas, em geral, podem ter:</strong>
            <ul class="text">
              <li>No humor:</li>Ansiedade, apatia, culpa, descontentamento geral,
                                desesperança, mudanças de humor, perda de interesse, perda de
                                interesse ou prazer nas atividades, solidão, tristeza, tédio ou sofrimento
                                emocional
              <li>No comportamento:</li>Agitação, automutilação, choro excessivo,
                                        inquietação, irritabilidade ou isolamento social
              <li>No sono:</li> Despertar precoce, excesso de sonolência, insônia ou sono agitado
              <li>Na cognição:</li> Falta de concentração, lentidão durante atividades ou pensamentos suicidas
              <li>No corpo:</li> Fadiga ou fome excessiva
              <li>No peso:</li> Ganho de peso ou perda de peso
            </ul>
            <strong class="sub-title">Ja especificando em faixa etaria, pode-se observar:</strong>
              <ul class="text">
                <li>Em crianças:</li>
                    Irritabilidade;
                    <br/> Redução no interesse em brincadeiras;
                    <br/> Queda no rendimento escolar;
                    <br/> Sensação de cansaço;
                    <br/> Ansiedade;
                    <br/> Dores somatizadas.
                <li>Em adolescentes:</li>
                    Agressividade;
                    <br/> Alteração do apetite;
                    <br/> Pensamento suicida;
                    <br/> Isolamento social.
                    <br/><br/>
                <li>Em adultos:</li>
                    Isolamento social;
                    <br/> Tristeza;
                    <br/> Alteração no humor;
                    <br/> Alteração no sono;
                    <br/> Perda de líbido.
              </ul>
            </div>
          </div>
        </div>
      );
    }
  }



