import React from 'react';
import axios from 'axios'
import { Link, Redirect } from 'react-router-dom';
import { Button, FormGroup, FormControl, ControlLabel } from "react-bootstrap";
import "../../styles/css/Form.css";

export default class Login extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      senha: "",
      isLogged: false
    };
  }

  validateForm() {
    return this.state.email.length > 0 && this.state.senha.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  handleSubmit = event => {
	const user = {
		email: this.state.email,
		senha: this.state.senha
	}

	axios.get('https://bemapi.herokuapp.com/usuario/autorizar?email=' + user.email + '&senha='+ user.senha)
	.then(res =>{
    var token = res.data.token;
    sessionStorage.setItem('token', token);
    var config = {
      headers: {
        'Authorization': 'Bearer ' + token
      }
    }

    axios.get('https://bemapi.herokuapp.com/usuario/autenticado', config)
    .then(res => {
      sessionStorage.setItem('papel_id', res.data.papel_id);
      this.setState({
        isLogged: true
      });
    })
    .catch(error => {
      console.log(error.response);
    })
	})
	.catch(error =>{
    console.log(error.response);
		if(error.response.status === 401){
      alert('Email ou Senha Invalidos');
    }
	})

    event.preventDefault();
  }

  render(){
    if(this.state.isLogged){
      return <Redirect to='./' />
    }
    return(
        <div className="container Login">
        <div className="page">
        <h1>Login</h1>
          <form onSubmit={this.handleSubmit}>
            <FormGroup controlId="email" bsSize="large">
              <ControlLabel>Email</ControlLabel>
              <FormControl
                autoFocus
                type="email"
                value={this.state.email}
                onChange={this.handleChange}
              />
            </FormGroup>
            <FormGroup controlId="senha" bsSize="large">
              <ControlLabel>Senha</ControlLabel>
              <FormControl
                value={this.state.senha}
                onChange={this.handleChange}
                type="password"
              />
            </FormGroup>
            <Button
              block
              bsSize="large"
              disabled={!this.validateForm()}
              type="submit"
            >
              Login
            </Button>
            <Link to = "/Cadastro"><a>Não Possui Cadastro?</a></Link>
          </form>
        </div>
        </div>
      );
  }
}



