import React from 'react';
import axios from 'axios'
import { Redirect } from 'react-router-dom';
import { Button, FormGroup, FormControl, ControlLabel } from "react-bootstrap";
import "../../styles/css/Form.css";

export default class Login extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      nome: "",
      email: "",
      senha: "",
      papel_id: 2,
      crp: "",
      abordagem_id: "",
      endereco: "",
      isAdd: false
    };
  }

  validateForm() {
    return this.state.email.length > 0 && this.state.senha.length > 0 && this.state.nome.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
    console.log(this.state);
  }

  handleSubmit = event => {
      const user = {
          nome: this.state.nome,
          email: this.state.email,
          senha: this.state.senha,
          papel_id: this.state.papel_id,
          psicologo: {
            crp: this.state.crp,
            abordagem_id: this.state.abordagem_id,
            endereco: this.state.endereco
          }
      }
      console.log(user);
      axios.post('https://bemapi.herokuapp.com/usuario', user)
      .then(res => {
          if(res.status === 200){
            this.setState({
              isAdd: true
            });
            alert('Cadastro realizado com sucesso!');
          }
      }).catch(error => {
        console.log(error.response);
          if(error.response.status === 500){
            alert('Email já registrado!')
          }
      })
    event.preventDefault();
  }

  render(){
    if(this.state.isAdd){
      return <Redirect to='./' />
    }
    return(
      <div className="container page">
      <div className="Cadastro">
      <h1>Cadastro</h1>
        <form onSubmit={this.handleSubmit}>
          <FormGroup controlId="nome" bsSize="large">
            <ControlLabel>Nome</ControlLabel>
            <FormControl
              autoFocus
              type="nome"
              value={this.state.nome}
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup controlId="email" bsSize="large">
            <ControlLabel>Email</ControlLabel>
            <FormControl
              autoFocus
              type="email"
              value={this.state.email}
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup controlId="senha" bsSize="large">
            <ControlLabel>Senha</ControlLabel>
            <FormControl
              value={this.state.senha}
              onChange={this.handleChange}
              type="password"
            />
          </FormGroup>
          <FormGroup controlId="crp" bsSize="large">
            <ControlLabel>CRP</ControlLabel>
            <FormControl
              value={this.state.crp}
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup controlId="abordagem_id" bsSize="large">
            <ControlLabel>Tipo de Abordagem</ControlLabel>
            <FormControl
              value={this.state.abordagem_id}
              onChange={this.handleChange}
            />
          </FormGroup>
          {/*<FormGroup controlId="telefone" bsSize="large">
            <ControlLabel>Telefone</ControlLabel>
            <FormControl
              value={this.state.telefone}
              onChange={this.handleChange}
            />
          </FormGroup>*/}
          <FormGroup controlId="endereco" bsSize="large">
            <ControlLabel>Endereço</ControlLabel>
            <FormControl
              value={this.state.endereco}
              onChange={this.handleChange}
            />
          </FormGroup>
          {/*<FormGroup controlId="papel_id" bsSize="small">
            <ControlLabel>Tipo de Cadastro</ControlLabel>
            <FormControl componentClass="select" value={this.state.papel_id} onChange={this.handleChange} type="papel_id">
              <option value="0">Psicologo</option>
              <option value="1">Administrador</option>
            </FormControl>
          </FormGroup> */}
          <Button
            block
            bsSize="large"
            disabled={!this.validateForm()}
            type="submit"
          >
            Cadastrar
          </Button>
        </form>
      </div>
      </div>
    );
  }
}



