import React from 'react';
import "../../styles/css/Transtornos.css";
import image3 from '../../styles/images/estresse.jpg'

export default class Estresse extends React.Component{

  render(){
    return(
      <div class="container">
        <img class="image" src={image3}/>
        <h1 class="font">Estresse</h1>
        <div class="row">
          <div class="col-md-4"><h4 class="text-center sub-title">O que é?</h4>
            <p class="text">O estresse é o desgaste físico e mental causado pela soma de respostas físicas e mentais
            causadas por determinados estímulos externos (estressores) e que permitem ao indivíduo
            (humano ou animal) superar determinadas exigências do meio em que vive.</p>
            <p class="text">O estresse pode ser causado pela ansiedade e pela depressão devido à mudança brusca no
            estilo de vida e a exposição a um determinado ambiente, que leva a pessoa a sentir um
            determinado tipo de angústia. Quando os sintomas de estresse persistem por um longo intervalo
            de tempo, podem ocorrer sentimentos de evasão (ligados à ansiedade e depressão).
            Os nossos mecanismos de defesa passam a não responder de uma forma eficaz,
            aumentando assim a possibilidade de vir a ocorrer doenças, especialmente cardiovasculares.</p>
          </div>
          <div class="col-md-4"><h4 class="text-center sub-title">Causas</h4>
            <strong class="sub-title">Causas internas:</strong>
                <p class="text">Ligados a características de personalidade, como perfeccionismo,
                pressa, querer fazer tudo ao mesmo tempo.</p>
            <strong class="sub-title"> Causas externas:</strong>
                <p class="text">Do ambiente. Mudanças em geral, até mesmo as positivas,
                desencadeiam estresse – porque exigem adaptação: nascimento de filho, mudanças profissionais
                (troca de emprego, promoção, demissão), aposentadoria, mudança de casa, divórcio, doença ou
                morte de pessoas queridas. Mas há também os pequenos, como o trânsito, que pode acabar
                tendo um peso importante para muitas pessoas.</p>
              <p class="text">"Quão estressante é um fator depende do fator em si, do significado que tem para
              a pessoa e da forma que ela  lida com ele", comenta a dra. Selma Bordin, psicóloga do Hospital Israelita Albert Einstein.</p>
          </div>
          <div class="col-md-4"><h4 class="text-center sub-title">Sintomas</h4>
              <ul class="text">
                <li>Sensação de desgaste constante</li>
                <li>Alteração de sono (dormir demais ou pouco)</li>
                <li>Tensão muscular</li>
                <li>Formigamento (na face ou nas mãos, por exemplo)</li>
                <li>Problemas de pele</li>
                <li>Hipertensão</li>
                <li>Mudança de apetite</li>
                <li>Alterações de humor</li>
                <li>Perda de interesse pelas coisas</li>
                <li>Problemas de atenção, concentração e memória</li>
                <li>Ansiedade</li>
                <li>Depressão</li>
              </ul>
              <strong class="sub-title">Veja o potencial estressante de algumas situações:</strong>
            <ul class="text">
              <li>Morte do cônjuge - 100</li>
              <li>Divórcio - 73</li>
              <li>Promrisão - 63</li>
              <li>Morte de um parente querido - 63</li>
              <li>Casamento - 50</li>
              <li>Demissão do trabalho - 47</li>
              <li>Aposentadoria - 45</li>
              <li>Reconciliação conjugal - 45</li>
              <li>Gravidez - 40</li>
              <li>Grandes conquistas pessoais - 28</li>
              <li>Problemas com o chefe - 23</li>
              <li>Férias - 13</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}



